    color: white;
    outline: none;
    border: none;
    padding: 8px 35px;
    border-radius: 0;
    margin-top: 25px;
    margin-bottom: 25px;
    -webkit-transition-duration: 0.4s;
         -o-transition-duration: 0.4s;
            transition-duration: 0.4s; }
    .Home .btn-try:hover, .Home .btn-try:active, .Home .btn-try:focus {
