.Contact {
  padding-top: 40px;
  min-height: calc(100vh - 60px - 120px); }
  .Contact ul {
    list-style: none;
    padding: inherit; }
    .Contact ul li {
      margin: 12px; }
      .Contact ul li span {
        border-radius: 50%;
        width: 48px;
        height: 48px;
        display: inline-block;
        position: relative;
        cursor: pointer;
        -webkit-transition-duration: 0.4s;
             -o-transition-duration: 0.4s;
                transition-duration: 0.4s;
        color: white; }
        .Contact ul li span:hover {
          -webkit-transform: rotate(360deg);
              -ms-transform: rotate(360deg);
                  transform: rotate(360deg);
          opacity: 0.8; }
        .Contact ul li span.ic-facebook {
          background-color: #3b5998;
          border: 2px solid #3b5998; }
        .Contact ul li span.ic-google {
          background-color: #d34836;
          border: 2px solid #d34836; }
        .Contact ul li span.ic-github {
          background-color: #272727;
          border: 2px solid #272727; }
        .Contact ul li span.ic-linkedin {
          background-color: #0077b5;
          border: 2px solid #0077b5; }
        .Contact ul li span.ic-twitter {
          background-color: #00aced;
          border: 2px solid #00aced; }
        .Contact ul li span a {
          display: block;
          width: 100%;
          height: 100%;
          color: inherit; }
        .Contact ul li span i {
          font-size: 1.3em;
          position: absolute;
          top: 50%;
          left: 50%;
          -webkit-transform: translate(-50%, -50%);
              -ms-transform: translate(-50%, -50%);
                  transform: translate(-50%, -50%); }

@media only screen and (max-width: 576px) {
  .Contact ul li span {
    width: 35px;
    height: 35px; } }

@media only screen and (max-width: 400px) {
  .Contact ul {
    display: none !important; } }



